/* import */
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,200;1,500;1,800&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

h2{
    font-weight: 500;
}

h1{
    font-weight: 800;
}

p{
    font-weight: 200;
}

/* colors */

$color-princepal : #2357a0;
/* $color-princepal : #03174cAA; */
$color-secondary : lighten($color-princepal , 10);
$color-white : #fff;
$color-black : #000;



.container{
    background-color: $color-princepal;
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 20% 80%;
    color: $color-white;
    transition: .2s;




    @media screen and (max-width:1024px){
        grid-template-columns: 15% 85%;
    }

    @media screen and (max-width:414px){
        grid-template-columns: 0 100%;

    }


    &_nav-bar{
        background-color: $color-secondary;
        display: grid;
        position: relative;
        /* border-top-right-radius: 20px; */
        filter: drop-shadow(10px 10px 15px $color-black);
        z-index: 100;

        &_logo{
            margin: 20px auto;
            background-color: $color-princepal;
            width: 60%;
            height: 70%;
            overflow: hidden;
            border-radius: 10px;
            filter: drop-shadow(10px 10px 15px $color-black);
            cursor: pointer;
            border-top: 1px solid rgba(255 ,255 ,255 ,0.5);
            border-left: 1px solid rgba(255 ,255 ,255 ,0.5);
            
            img{
                border-radius: 10px;
                width: 100%;
                height: 100%;
            }
        }

        

        &_menu{
            display: grid;

            ul{
                margin: 0 auto;
                width: 100%;
                overflow: hidden;

                a{
                    text-decoration: none;
                    color: $color-white;
                    margin-left: 5px;
                    

                    li{
                        filter: drop-shadow(10px 10px 5px $color-black);
                        list-style: none;
                        padding: 20px;
                        width: 100%;
                        margin-left: 10px;
                        background-color: $color-princepal;
                        border-bottom-left-radius: 20px;
                        border-top-left-radius: 20px;
                        cursor: pointer;
                        transition: .3s all;
                        border-top: 1px solid rgba(255 ,255 ,255 ,0.5);
                        border-left: 1px solid rgba(255 ,255 ,255 ,0.5);

                        i{
                            font-size: 20px;
                            list-style: none;
                        }
    
    
                    }


                }



                li:hover {
                    color: #000;
                    padding: 20px 40px;
                }
                li:hover a{
                    color: #000;
                }
            }
        }



 

        @media screen and (max-width:1024px){

            

            &_logo{
                height: 30%;
                width: 80%;
            }

            &_menu{    
                ul{    
                    align-items: center;

                    a{
                        text-decoration: none;
                        color: $color-white;
                        text-align: center;

                        li{

                            width: 80%;
                            border-radius: 20px;
                            i{    
                                display: flex;
                                justify-content: center;
                                font-size: 35px;
    
                            }
        
                        }
                    }

                }
            }
        }

        &_bottem{
            text-align: center;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }

    &_contante{
/*         display: flex;
        align-items: center;
        justify-content: center; */
        background-image: url(../images/IMG-0829.jpg);
        background-position:0px -65px;
        /* background-position:top; */
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        /* position: relative; */

        @media screen and (max-width:414px){
        
            background-position:0px ;
        }

        

        &_main{
            background: linear-gradient(135deg, rgba(255 ,255 ,255 ,0.1), rgba(255 ,255 ,255 ,0.1));
            backdrop-filter: blur(4px);
            border-radius: 10px;
            position: absolute;
            top: 10%;
            bottom: 10%;
            left: 10%;
            right: 10%;
            filter: drop-shadow(10px 10px 10px $color-black);
            border-top: 1px solid rgba(255 ,255 ,255 ,0.5);
            border-left: 1px solid rgba(255 ,255 ,255 ,0.5);
            display: flex;
            align-items: center;
            justify-content: center;

            &_about{

                /* background-color: #000; */
                height: 90%;
                width: 90%;
                overflow: hidden;
                
                &_titel{
                    display: flex;
                    justify-content: center;
                    h1{
                        font-size: 35px;
                        font-weight: 500;
                    }

                    @media screen and (max-width:414px){
                        
                        h1{
                            font-size: 25px;
                        }
                    }
                }

                &_cv{
                    ul{
                        margin-top: 2%;
                        display: grid;
                        grid-template-columns: 1fr ;
                        gap: 20px;
    
                        li{
                            text-align: center;
                            background: linear-gradient(135deg, #2357a023 , #2357a023);
                            backdrop-filter: blur(4px);
                            padding: 15px 25px;
                            border-radius: 10px;
                            list-style: none;
                            border-top: 1px solid rgba(255 ,255 ,255 ,0.5);
                            border-left: 1px solid rgba(255 ,255 ,255 ,0.5);
                            cursor: default;
                            
                        }
                        
                        @media screen and (max-width:414px){
                            
                            gap: 10px;
                            li{
                                text-align: center;
                                padding: 10px 20px;
                                font-size: 10px;
                                font-weight: 200;
                                letter-spacing: 1px;
                                
                            }
                        }

                        @media screen and (max-width:540px){
                            
                            gap: 10px;
                            li{
                                text-align: center;
                                padding: 10px 20px;
                                font-size: 13px;
                                font-weight: 200;
                                letter-spacing: 1px;
                            }
                        }

/*                         @media screen and (max-width:10240px){
                            
                            gap: 10px;
                            li{
                                text-align: center;
                                padding: 15px 25px;
                                font-size: 15px;
                                font-weight: 200;
                                letter-spacing: 1px;
                            }
                        } */
                    }
                }
            }
        }
        



        #Open{
            color: $color-white;
            font-size: 40px;
            position: absolute;
            top: 5px;
            left: 5px;
            display: none;
        }

        #Close{
            color: $color-white;
            font-size: 40px;
            position: absolute;
            top: 5px;
            right: 5px;
            display: none;
        }


        @media screen and (max-width:414px){
        
    
            #Open{
                    color: $color-white;
                    font-size: 40px;
                    position: absolute;
                    top: 5px;
                    left: 5px;
                    display: block;
                    z-index: 10000;
                }
        }

    }
}


.Active{
    grid-template-columns: 30% 70%;
}
